<template>
  <div class="conts-area">
    <div class="conts-tit">
      <h2>  </h2>
    </div>
    <div class="tbl-area">
      <!-- table list start -->
      <table class="tbl">
        <caption>  </caption>
        <colgroup>
          <col style="width:30%">
          <col style="width:70%">
        </colgroup>
        <tbody>
          <tr>
            <th></th>
            <td class="t-left">
              <select id="s1" title="select" class="wp20" v-model="member" @change="onChange">
                <option value=""></option>
                <option v-for="(item, i) in list" v-bind:key="i" v-bind:value="item">{{ item.id }}</option>
              </select>
              <input type="text" id="name" v-model="member.name" readonly>
            </td>
          </tr>
          <tr>
            <th></th>
            <td class="t-left">
              <input type="password" id="pwd1" v-model="pwd1">
            </td>
          </tr>
          <tr>
            <th> </th>
            <td class="t-left">
              <input type="password" id="pwd2" v-model="pwd2">
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <br />
    <div class="t-center">
      <button type="button" class="btn btn-blue" @click="pwdSave"></button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'PwdChg',
  data: () => ({
    list: [],
    member: '',
    pwd1: '',
    pwd2: ''
  }),
  methods: {
    memberList: function () {
      axios.get('/api/mypage/memberList')
        .then(res => {
          this.list = res.data.data
        }).catch(() => {
          alert(' .   .')
        })
    },
    pwdSave: function () {
      if (this.member === '') {
        alert(' .')
      } else if (this.pwd1 === '' || this.pwd2 === '') {
        alert('  .')
      } else {
        if (this.pwd1 !== this.pwd2) {
          alert('  .')
        } else {
          this.updatePassword()
        }
      }
    },
    updatePassword: function () {
      axios.post('/api/mypage/passwordChange', {
        id: this.member.id,
        pwd1: this.pwd1,
        pwd2: this.pwd2
      })
        .then(res => {
          if (res.data.success) {
            alert(' .')
            this.pwd1 = ''
            this.pwd2 = ''
          }
        }).catch(() => {
          alert(' .   .')
        })
    },
    onChange: function () {
      this.pwd1 = ''
      this.pwd2 = ''
    }
  },
  created: function () {
    this.memberList()
  }
}
</script>

