import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Test from '../views/Test.vue'
import Login from '../components/Login.vue' // 
import MyPage from '../views/mypage/MyPage.vue' //    vue
import AppReceive from '../views/mypage/AppReceive.vue' //  > 
import AppSend from '../views/mypage/AppSend.vue' //  > 
import PwdChg from '../views/mypage/PwdChg.vue' //  >  
import Plando from '../views/plando/Plando.vue' //    vue
import DeptPlando from '../views/plando/DeptPlando.vue' //  >   
import MyPlandoView from '../views/plando/MyPlandoView.vue' //  >  
import MyPlandoRegi from '../views/plando/MyPlandoRegi.vue' //  >  
import GoStop from '../views/gostop/GoStop.vue' //   vue
import MonthGostopView from '../views/gostop/MonthGostopView.vue' //  >   
import GostopRegi from '../views/gostop/GostopRegi.vue' //  >  
import MyMonthView from '../views/gostop/MyMonthView.vue' //  >     
import YearView from '../views/gostop/YearView.vue' //  >    
import Vcode from '../views/vcode/Vcode.vue' //   vue
import CommonCode from '../views/vcode/CommonCode.vue' //  >  
import CalendarView from '../views/vcode/CalendarView.vue' //  >  
import DeptMember from '../views/vcode/DeptMember.vue' //  >   
import MemberRegi from '../views/vcode/MemberRegi.vue' //  >  
import HolidayRegi from '../views/vcode/HolidayRegi.vue' //  >  
import GostopForceRegi from '../views/vcode/GostopForceRegi.vue' //  >  
import GostopForceDel from '../views/vcode/GostopForceDel.vue' //  >  
import InsertAcnt from '../views/vcode/InsertAcnt.vue' //  >    
import DeptGostopView from '../views/gostop/DeptGostopView.vue' //  >   
import Template from '../views/vcode/Template.vue' //  >  
import AdminPwdChg from '../views/vcode/AdminPwdChg.vue' //  >   
// import store from '../store/index.js'

Vue.use(VueRouter)

const requireAuth = () => (to, from, next) => {
  // console.log('login', store.state.loginState)
  // console.log('login', sessionStorage.chiefYn)
  if (sessionStorage.chiefYn === 'Y') {
    return next()
  }
  alert('   .')
  next(false)
}

const requireAuth2 = () => (to, from, next) => {
  if (sessionStorage.chiefYn === 'Y' || sessionStorage.dept === '1') {
    return next()
  }
  alert('    .')
  next(false)
}

const routes = [
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // },
  {
    path: '/test',
    name: 'Test',
    component: Test
  },
  {
    path: '/',
    component: Login,
    name: 'Login'
  },
  // {
  //   path: '/login',
  //   component: Login
  // },
  {
    path: '/mypage',
    component: MyPage,
    children: [
      {
        path: '/mypage/receive',
        component: AppReceive,
        name: 'AppReceive'
      },
      {
        path: '/mypage/send',
        component: AppSend
      },
      {
        path: '/mypage/pwdchg',
        component: PwdChg
      }
    ]
  },
  {
    path: '/plando',
    component: Plando,
    children: [
      {
        path: '/plando/dept',
        component: DeptPlando
      },
      {
        path: '/plando/view',
        component: MyPlandoView
      },
      {
        path: '/plando/regi',
        component: MyPlandoRegi
      }
    ]
  },
  {
    path: '/gostop',
    component: GoStop,
    children: [
      {
        path: '/gostop/view',
        component: MonthGostopView
      },
      {
        path: '/gostop/regi',
        component: GostopRegi
      },
      {
        path: '/gostop/mymonth',
        component: MyMonthView
      },
      {
        path: '/gostop/year',
        component: YearView,
        beforeEnter: requireAuth()
      },
      {
        path: '/gostop/dept',
        component: DeptGostopView,
        beforeEnter: requireAuth2()
      }
    ]
  },
  {
    path: '/vcode',
    component: Vcode,
    children: [
      {
        path: '/vcode/common',
        component: CommonCode
      },
      {
        path: '/vcode/calendar',
        component: CalendarView
      },
      {
        path: '/vcode/member',
        component: DeptMember
      },
      {
        path: '/vcode/regi',
        component: MemberRegi
      },
      {
        path: '/vcode/insertAcnt',
        component: InsertAcnt
      },
      {
        path: '/vcode/holiday',
        component: HolidayRegi
      },
      {
        path: '/vcode/forceRegi',
        component: GostopForceRegi
      },
      {
        path: '/vcode/forceDel',
        component: GostopForceDel
      },
      {
        path: '/vcode/template',
        component: Template
      },
      {
        path: '/vcode/adminPwdChg',
        component: AdminPwdChg
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
